@use 'base/variables' as v;
@use 'base/mixins' as m; 

.gallery {
    @include m.container;

    &-img {
        @include m.grid(1, 2rem);
        @include m.mobile {
            @include m.grid(2, 2rem);
        }
        @include m.tablet {
            @include m.grid(3, 2rem);
        }
        @include m.desktop {
            @include m.grid(4, 2rem);
        }
        @include m.desktopXL {
            @include m.grid(4, 2rem);
        }

        img {
            height: 20rem;
            object-fit: cover;
        }
    }
}